@font-face {
  font-family: 'Cinnamon Coffe Coke';
  src: url("assets/CinnamonCoffeCake.ttf");
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
  'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
  sans-serif;
  font-size: 20px;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

#root {
  display: flex;
  width: 100%;
  height: auto;
  min-height: 100vh;
}

/* Navigation bar styling */
#root > nav {
  /* container */
  width: 42px;
  height: inherit;
  padding: 7px;
  overflow: hidden;
  white-space: nowrap;
  /* appearance */
  background: rgb(2,0,36);
  background: linear-gradient(65deg, rgba(196,113,113,1) 0, rgba(2,0,36,1) 95%);
  font-size: 22px;
  /* animation */
  transition: 300ms;
}

#root > nav:hover, #root > nav:focus {
  outline: none;    /* don't show border when focused */
  width: 250px;
}

#root > nav > ul {
  margin: 0;
  padding: 0;
  list-style-type: none;
}

#root > nav > ul > li {
  margin-bottom: 10px;
}

#root > nav > ul > li > button {
  /* override default */
  background-color: transparent;
  border: none;
  font-family: "Cinnamon Coffe Coke", serif;
  cursor: pointer;
  /* container */
  display: flex;
  align-items: center;
  padding: 0;
  width: 100%;
  /* appearance */
  font-size: 24px;
  color: white;
}

#root > nav > ul > li > button > img {
  max-width: 40px;
  transition: all 50ms;
}

#root > nav > ul > li > button > img.nav-item-selected {
  max-width: 37px;
  border: 2px solid #ff6b87;
}

#root > nav > ul > li > button > button {
  /* override default */
  background-color: transparent;
  border: none;
  font-family: inherit;
  font-size: inherit;
  color: inherit;
  /* container */
  flex-grow: 1;
  margin: 0 8px;
  padding: 5px;
  /* animation */
  transition: 100ms;
}

#root > nav > ul > li > button > button:hover, #root > nav > ul > li > button > button.nav-item-selected {
    background-color: rgba(106, 90, 205, 0.30);
    background-image: url("assets/nav-bar/hover.gif");
}

/* This is for tab users. It has to be disabled for mobile devices,
   because they don't support focus-visible. */
@media (hover: hover) {
    #root > nav:has(button:focus-visible) {
        outline: none;    /* don't show when focused */
        width: 250px;
    }

    #root > nav > ul > li > button > button:focus-visible {
        background-color: rgba(106, 90, 205, 0.30);
        background-image: url("assets/nav-bar/hover.gif");
    }
}

#root > main {
  flex-grow: 1;
  padding: 4px 8px;
}
